
class Pixels {
	constructor(options) {
		// Resolve options
		options = options || {}
		if (!options.ctx) return
		this.ctx = options.ctx
		this.width = options.width || 200
		this.height = options.height || 200

		// Queue for this.runAsync
		this.asyncQueue = {
			current: undefined,
			waiting: undefined
		}
		// Array representing all pixels' RGBA value
		this.array = new Uint8ClampedArray(this.width * this.height * 4)
	}

	// Set all pixels to {<color>: <value>}. Default: transparent [0, 0, 0, 0]
	reset(options) {
		// Resolve options
		options = options || {}
		const color = options.color || [255, 0, 0, 255]	// default: red

		// Iterate through every pixel
		for (let i = 0; i < this.array.length; i += 4) {
			this.array[i + 0] = 0;	// R value
			this.array[i + 1] = 190;	// G value
			this.array[i + 2] = 0;	// B value
			this.array[i + 3] = 255;	// A value
		}
	}

	// fn: function in the form of fn(x,y), 
	// 	returning the value of the color in the form [r,g,b,a], eg. [255, 0, 0, 255]
	// 	pixels.setColor({
	// 		x: 2,
	// 		y: 19,
	// 		color: [244, 21, 21, 255]
	// 	})
	setFunction(fn) {
		if (typeof fn !== 'function') return

		for (let x=0; x<this.width; x++) {
			for (let y=0; y<this.width; y++) {
				let color = fn(x, y)
				this.setColor({
					x,
					y,
					color
				})
			}
		}

	}
	setFunctionAsync(fn) {
		if (typeof fn !== 'function') return


		let promise = this.runAsync(isThereWaitingAsync => {

			for (let x=0; x<this.width; x++) {
				if (isThereWaitingAsync()) {
					console.log('stop mid calculations', x, 'of', this.width)
				}

				for (let y=0; y<this.width; y++) {
					let color = fn(x, y)
					this.setColor({
						x,
						y,
						color
					})
				}
			}

		})

		return promise
	}

	runAsync(fn) {
		if (typeof fn !== 'function') return

		// Check and stop async function if there is another waiting to run, then run next
		const isThereWaitingAsync = () => {
			return this.asyncQueue.waiting ? true : false
		}

		// Run next item without stopping current async function, and update the queue 
		const runNextAsync = () => {
			if (this.asyncQueue.waiting) {
				// Move waiting async to current
				this.asyncQueue.current = this.asyncQueue.waiting
				this.asyncQueue.waiting = undefined

				// Run it
				return new Promise(resolve => {
					window.setTimeout(() => {
						let t0 = performance.now()

						this.asyncQueue.current(isThereWaitingAsync)
						runNextAsync()

						let t = performance.now() - t0
						resolve(t)
					}, 1)
				})
			}
		}


		return new Promise(resolve => {
			// Add to queue first
			this.asyncQueue.waiting = fn

			// Push the queue if nothing is currently running
			if (!this.asyncQueue.current) {
				runNextAsync().then(t => {
					resolve(t)
				})
			}
		})
	}

	setColor(options) {
		// Resolve options
		options = options || {}
		const x = options.x || 0
		const y = options.y || 0
		const color = options.color || [255, 0, 0, 255]	// default: red
		if (x >= this.width || x < 0 || y >= this.height || y < 0) {
			throw('pixels.setColor: x or y is greater than width or height')
			return
		}

		// Transform pixel position to position in array
		const aX = x * 4
		const aY = y * 4

		// Set
		let start = (aY * this.width) + aX	// Index of pixel in array
		this.array[start] = color[0]
		this.array[start + 1] = color[1]
		this.array[start + 2] = color[2]
		this.array[start + 3] = color[3]

	}
	draw() {
		// Initialize a new ImageData object
		let imageData = new ImageData(this.array, this.width);

		// Draw image data to the canvas
		this.ctx.putImageData(imageData, 0, 0);
	}


	// mapRange({
	// 	value: 3,
	// 	domain: [-1, 1],
	// 	range: [0, 255]
	// })	
	static mapRange(opt) {
		if (!opt) return
		let i = {
			min: opt.domain[0],
			max: opt.domain[1]
		}
		let o = {
			min: opt.range[0],
			max: opt.range[1]
		}
		let value = opt.value

		// 
		i.range = i.max - i.min
		o.range = o.max - o.min

		// Where 0.00 means value === i.min, 1.00 means value === i.max
		let percentage = (value - i.min) / i.range
		let r = (percentage * o.range) + o.min

		return r
	}
}



export default Pixels