<template>
	<div class="color-picker">
		<div>
			<canvas ref="canvas" :width="width" :height="height"></canvas>
		</div>
		<div>
			<label>
				<span>L*</span>
				<input type="range" min="0" max="100" step="1" v-model="inputs.l">
				<span v-text="inputs.l"></span>
			</label>
			<label>
				<span>scale</span>
				<input type="number" min="0.02" max="10" v-model="inputs.scale">
				<span v-text="inputs.scale"></span>
			</label>
			<samp v-text="status"></samp>
		</div>


		<footer>Based on: https://github.com/Evercoder/culori</footer>
	</div>
</template>

<script>
const culori = require('culori')

import Pixels from './ColorPicker/Pixels.js'	// Wraps the canvas

export default {
	name: 'ColorPicker',
	data() {
		return {
			inputs: {
				l: 80,
				scale: 1
			},
			width: 400,
			height: 400,
			status: ''
		}
	},
	computed: {
		ctx() {
			return this.$refs.canvas.getContext('2d')
		}
	},
	methods: {
		draw() {
			// 
			let pixels = new Pixels({
				ctx: this.ctx,
				width: this.width,
				height: this.height
			})


			let domain = [0, this.width]	// Input (position)
			let range = [-112, 112]	// Output (color LAB)
			let culoriRgb = culori.converter('rgb')

			pixels
				.setFunctionAsync((x, y) => {

					let colorLAB = {
						mode: 'lab',
						l: + this.inputs.l,
						a: Pixels.mapRange({value: x, domain, range}),	// [0..112)
						b: Pixels.mapRange({value: y, domain, range}),
						alpha: 1
					}

					// Set pixel transparent if not displayable in sRGB
					if (culori.displayable(colorLAB) === false) {
						return [0, 0, 0, 0]
					}

					// Convert
					let colorRGB = culoriRgb(colorLAB)
					let rgb = [
						colorRGB.r * 255, 
						colorRGB.g * 255, 
						colorRGB.b * 255
					]

					return [...rgb, 255]
				})
				.then(t => {
					this.status = `Rendered in ${t.toFixed(0)}ms`
					pixels.draw()
				})
		}
	},
	watch: {
		inputs: {
			handler() {
				this.draw()
			},
			deep: true
		}
	},
	mounted() {
		this.draw()
	}
}
</script>

<style>
.color-picker canvas {
	/*background: hsl(0, 0%, 40%);*/
	width: 300px;
	height: 300px;
	border: 1px solid hsl(0, 0%, 90%);
	/*image-rendering: crisp-edges;*/
}
</style>